import Vue from 'vue'
import Vuex from 'vuex'
import {
  menuList
} from "@/mock/home/aside/index.js";


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    menuList,
    asideWidth: 200,
    currentTabId: "1", //tabArray:tab.id
    tabArray: [{
        title: "Tab 1",
        id: "1",
        content: "Tab 1 content"
      },
      {
        title: "Tab 2",
        id: "2",
        content: "Tab 2 content"
      }
    ]
  },
  actions: {
    setCurrentPath({
      commit
    }, path) {
      return commit('setCurrentPath', path)
    }
  },
  mutations: {
    setAsideWidth(state, width) {
      localStorage.setItem("store.state", JSON.stringify(state));
      state.asideWidth = width;
    },
    addTab(state, tab) {
      let tabArray = [...state.tabArray];
      let isAddTab = tabArray.some(tabInArray => {
        return tabInArray.id === tab.id
      });
      state.currentTabId = tab.id;
      if (isAddTab) {
        return false;
      }
      tabArray.push(tab);
      state.tabArray = tabArray;
    },
    clickTab(state, tabVM) {
      state.currentTabId = tabVM.$props.name;
    },
    removeTab(state, tabId) {
      let tabs = state.tabArray;
      // 如果与currentID相同，则调整currentID
      if (state.currentTabId === tabId) {
        tabs.forEach((tab, index) => {
          if (tab.id === tabId) {
            let nextTab = tabs[index + 1] || tabs[index - 1];
            if (nextTab) {
              state.currentTabId = nextTab.id;
            }
          }
        });
      }
      state.tabArray = tabs.filter(tab => tab.id !== tabId);
    },
  }
})